'use client';

import { useState, useEffect } from 'react';
import { supabase } from '@/lib/supabaseClient';
import { useRouter } from 'next/navigation';
import dynamic from 'next/dynamic';
import toast, { Toaster } from 'react-hot-toast';
import * as turf from '@turf/turf';

const HazardMap = dynamic(() => import('@/components/HazardMap'), {
    loading: () => <div style={{ height: '100%', background: '#eee' }}>Âú∞Âõ≥Ë™≠Ëæº‰∏≠...</div>,
    ssr: false
});

// Â∑ùË∂äÂ∏Ç„Éá„Éº„Çø
const KAWAGOE_BOUNDARY = {
    type: 'Feature',
    properties: { name: 'Â∑ùË∂äÂ∏Ç' },
    geometry: {
        type: 'Polygon',
        coordinates: [
            [
                [139.4312, 35.9458],
                [139.4623, 35.9521],
                [139.4985, 35.9412],
                [139.5241, 35.9234],
                [139.5312, 35.8956],
                [139.5123, 35.8712],
                [139.4756, 35.8543],
                [139.4421, 35.8612],
                [139.4156, 35.8845],
                [139.3985, 35.9123],
                [139.4123, 35.9312],
                [139.4312, 35.9458]
            ]
        ]
    }
};

export default function AdminPage() {
    // ‚ñº‚ñº‚ñº Â§âÊõ¥ÔºöÊúÄÂàù„ÅØ„ÄåÁ´ã„Å°ÂÖ•„ÇäÁ¶ÅÊ≠¢(false)„Äç„Å´„Åô„Çã ‚ñº‚ñº‚ñº
    const [isAdmin, setIsAdmin] = useState(false);
    const [isChecking, setIsChecking] = useState(true); // „ÉÅ„Çß„ÉÉ„ÇØ‰∏≠„Éï„É©„Ç∞

    const [center, setCenter] = useState({ lat: 35.9251, lng: 139.4858 });
    const [zoom, setZoom] = useState(13);
    const [selectedBoundary, setSelectedBoundary] = useState<any>(null);

    const [allPosts, setAllPosts] = useState<any[]>([]);
    const [filteredPosts, setFilteredPosts] = useState<any[]>([]);

    const router = useRouter();

    // ‚ñº‚ñº‚ñº Âæ©Ê¥ªÔºöÁÆ°ÁêÜËÄÖÊ®©Èôê„ÉÅ„Çß„ÉÉ„ÇØ ‚ñº‚ñº‚ñº
    useEffect(() => {
        const checkAdmin = async () => {
            // 1. „É≠„Ç∞„Ç§„É≥„Åó„Å¶„ÅÑ„Çã„ÅãÁ¢∫Ë™ç
            const {
                data: { session }
            } = await supabase.auth.getSession();

            if (!session) {
                toast.error('„É≠„Ç∞„Ç§„É≥„Åó„Å¶„Åè„Å†„Åï„ÅÑ');
                router.push('/'); // „Éà„ÉÉ„Éó„Å∏ËøΩÊîæ
                return;
            }

            // 2. admin_users„ÉÜ„Éº„Éñ„É´„Å´ID„Åå„ÅÇ„Çã„ÅãÁ¢∫Ë™ç
            const { data, error } = await supabase.from('admin_users').select('*').eq('id', session.user.id).single();

            if (error || !data) {
                console.error('ÁÆ°ÁêÜËÄÖÊ®©Èôê„Å™„Åó:', session.user.id);
                toast.error('ÁÆ°ÁêÜËÄÖÊ®©Èôê„Åå„ÅÇ„Çä„Åæ„Åõ„Çì');
                router.push('/'); // „Éà„ÉÉ„Éó„Å∏ËøΩÊîæ
            } else {
                // 3. ÂêàÊ†ºÔºÅ
                setIsAdmin(true);
                setIsChecking(false);
                // ÂêàÊ†º„Åó„ÅüÂ†¥Âêà„ÅÆ„Åø„Éá„Éº„Çø„ÇíË™≠„ÅøËæº„ÇÄ
                fetchPosts();
            }
        };

        checkAdmin();
    }, [router]);

    // „Éá„Éº„ÇøÂèñÂæóÈñ¢Êï∞ÔºàË™çË®ºOKÂæå„Å´Âëº„Å∂„Åü„ÇÅÂ§ñ„Å´Âá∫„Åó„Åæ„Åó„ÅüÔºâ
    const fetchPosts = async () => {
        const { data, error } = await supabase.from('hazard_posts').select('*').order('created_at', { ascending: false });

        if (!error && data) {
            setAllPosts(data);
            setFilteredPosts(data);
        }
    };

    const handleDelete = async (id: number, e: React.MouseEvent) => {
        e.stopPropagation();
        if (!window.confirm('Êú¨ÂΩì„Å´„Åì„ÅÆÊäïÁ®ø„ÇíÂâäÈô§„Åó„Åæ„Åô„ÅãÔºü\n„Åì„ÅÆÊìç‰Ωú„ÅØÂèñ„ÇäÊ∂à„Åõ„Åæ„Åõ„Çì„ÄÇ')) return;

        const { error } = await supabase.from('hazard_posts').delete().eq('id', id);
        if (error) {
            toast.error('ÂâäÈô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
            return;
        }
        setAllPosts((prev) => prev.filter((post) => post.id !== id));
        setFilteredPosts((prev) => prev.filter((post) => post.id !== id));
        toast.success('ÊäïÁ®ø„ÇíÂâäÈô§„Åó„Åæ„Åó„Åü');
    };

    const handleSelectArea = (area: string) => {
        if (area === 'kawagoe') {
            const boundary = KAWAGOE_BOUNDARY;
            setCenter({ lat: 35.9251, lng: 139.4858 });
            setZoom(13);
            setSelectedBoundary(boundary);

            const insidePosts = allPosts.filter((post) => {
                const pt = turf.point([post.lng, post.lat]);
                const poly = boundary as any;
                return turf.booleanPointInPolygon(pt, poly);
            });
            setFilteredPosts(insidePosts);
            toast.success(`„Ç®„É™„Ç¢ÂÜÖ„ÅÆÊäïÁ®ø: ${insidePosts.length}‰ª∂`);
        } else {
            setSelectedBoundary(null);
            setFilteredPosts(allPosts);
            toast('ÂÖ®„Ç®„É™„Ç¢„ÇíË°®Á§∫„Åó„Åæ„Åô');
        }
    };

    // ‚ñº‚ñº‚ñº „ÉÅ„Çß„ÉÉ„ÇØ‰∏≠„ÅØ„É≠„Éº„Éá„Ç£„É≥„Ç∞ÁîªÈù¢„ÇíÂá∫„Åô ‚ñº‚ñº‚ñº
    if (isChecking) {
        return (
            <div style={{ height: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center', background: '#2c3e50', color: 'white' }}>
                <p>Ê®©Èôê„ÇíÁ¢∫Ë™ç‰∏≠...</p>
            </div>
        );
    }

    // Ê®©Èôê„Åå„Å™„ÅÑÂ†¥Âêà„ÅØ‰Ωï„ÇÇË°®Á§∫„Åó„Å™„ÅÑÔºàuseEffect„ÅßÈ£õ„Å∞„Åï„Çå„Çã„Åæ„Åß„ÅÆ„ÉÅ„É©„Å§„ÅçÈò≤Ê≠¢Ôºâ
    if (!isAdmin) return null;

    return (
        <div style={{ display: 'flex', height: '100vh', fontFamily: 'sans-serif' }}>
            <Toaster />

            {/* Â∑¶„Çµ„Ç§„Éâ„Éê„Éº */}
            <aside
                style={{
                    width: '320px',
                    background: '#2c3e50',
                    color: 'white',
                    display: 'flex',
                    flexDirection: 'column',
                    zIndex: 20,
                    boxShadow: '2px 0 10px rgba(0,0,0,0.3)'
                }}
            >
                <div style={{ padding: '20px', borderBottom: '1px solid #34495e' }}>
                    <h1 style={{ margin: 0, fontSize: '18px' }}>üõ°Ô∏è ÁÆ°ÁêÜÁîªÈù¢</h1>
                </div>

                {/* Êìç‰Ωú„Éë„Éç„É´ */}
                <div style={{ padding: '20px', borderBottom: '1px solid #34495e' }}>
                    <label style={{ display: 'block', marginBottom: '10px', fontSize: '14px', color: '#bdc3c7' }}>„Ç®„É™„Ç¢ÈÅ∏Êäû</label>
                    <select
                        onChange={(e) => handleSelectArea(e.target.value)}
                        style={{
                            width: '100%',
                            padding: '10px',
                            background: '#34495e',
                            color: 'white',
                            border: '1px solid #7f8c8d',
                            borderRadius: '4px'
                        }}
                    >
                        <option value="">ÂÖ®„Å¶„ÅÆ„Ç®„É™„Ç¢</option>
                        <option value="kawagoe">ÂüºÁéâÁúå Â∑ùË∂äÂ∏Ç</option>
                    </select>

                    <div style={{ marginTop: '15px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <span style={{ fontSize: '14px', color: '#bdc3c7' }}>Ë©≤ÂΩì‰ª∂Êï∞:</span>
                        <span style={{ fontSize: '20px', fontWeight: 'bold', color: '#e74c3c' }}>{filteredPosts.length}</span>
                    </div>
                </div>

                {/* ÊäïÁ®ø„É™„Çπ„Éà */}
                <div style={{ flex: 1, overflowY: 'auto', background: '#253342' }}>
                    {filteredPosts.map((post) => (
                        <div
                            key={post.id}
                            style={{
                                padding: '15px',
                                borderBottom: '1px solid #34495e',
                                cursor: 'pointer',
                                transition: '0.2s',
                                position: 'relative'
                            }}
                            onMouseOver={(e) => (e.currentTarget.style.background = '#34495e')}
                            onMouseOut={(e) => (e.currentTarget.style.background = 'transparent')}
                            onClick={() => {
                                setCenter({ lat: post.lat, lng: post.lng });
                                setZoom(16);
                            }}
                        >
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '5px', paddingRight: '30px' }}>
                                <span style={{ fontWeight: 'bold', fontSize: '14px', color: '#ecf0f1' }}>{post.reason}</span>
                            </div>

                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <span style={{ fontSize: '11px', color: '#95a5a6' }}>{new Date(post.created_at).toLocaleDateString()}</span>
                                <div style={{ display: 'flex', gap: '5px' }}>
                                    {post.tags?.map((tag: string) => (
                                        <span
                                            key={tag}
                                            style={{
                                                fontSize: '10px',
                                                background: '#34495e',
                                                padding: '2px 6px',
                                                borderRadius: '3px',
                                                color: '#bdc3c7'
                                            }}
                                        >
                                            {tag}
                                        </span>
                                    ))}
                                </div>
                            </div>

                            <button
                                onClick={(e) => handleDelete(post.id, e)}
                                style={{
                                    position: 'absolute',
                                    top: '15px',
                                    right: '15px',
                                    background: 'none',
                                    border: 'none',
                                    cursor: 'pointer',
                                    fontSize: '16px',
                                    opacity: 0.7
                                }}
                                title="ÂâäÈô§„Åô„Çã"
                            >
                                üóëÔ∏è
                            </button>
                        </div>
                    ))}

                    {filteredPosts.length === 0 && (
                        <div style={{ padding: '20px', textAlign: 'center', color: '#7f8c8d', fontSize: '14px' }}>ÊäïÁ®ø„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì</div>
                    )}
                </div>
            </aside>

            <main style={{ flex: 1, position: 'relative' }}>
                <HazardMap centerPos={center} zoomLevel={zoom} onMapChange={() => {}} posts={filteredPosts} boundary={selectedBoundary} />
            </main>
        </div>
    );
}
